<template>
  <div
    class="widgets"
    :style="{
      left: val.left + 'px' ,
      top:  val.top + 'px',
      width: val.width + 'px',
      height: val.height + 'px',
      textAlign: val.style.Alignment==1?'left':val.style.Alignment==2?'center':'right',
      fontSize: val.style.FontSize + 'pt',
      color: val.style.FontColor,
    }"
    style="position:absolute;overflow:hidden"
  >
    <table
      border="1"
      width="100%"
      cellspacing="0"
      cellpadding="2"
      style="border-collapse:collapse;font-size:12px;"
      bordercolor="#000000"
    >
      <tr>
        <th
          v-for="item in columns"
          :key="item.name"
          :width="item.name==='_seq'?40:''"
        >{{item.title}}</th>
      </tr>
      <tr>
        <td
          v-for="item in columns"
          :key="item.name"
          :rowspan="item.name==='_seq'?rowspan:''"
        >{{item.value}}</td>
      </tr>
      <tr v-for="item in rows" :key="item.name">
        <td :colspan="colspan" v-html="item.value"></td>
      </tr>
    </table>
  </div>
</template>

<script>
let itemAttr = {
  type: 'braid-table',
  isEdit: 0,
  title: '',
  name: 'details',
  value: '{details}',
  style: {},
  defaultValue: [
    {
      productName: '',
      brandName: 'Joyoung',
      specModel: '165L',
      productUnitName: '',
      weight: '0.1kg',
      volume: '10cm*10cm*10cm',
      quantity: 1,
      univalent: 190,
      salePrice: 100,
    },
    {
      productName: '002',
      brandName: 'Joyoung',
      specModel: '165L',
      productUnitName: '',
      weight: '0.1kg',
      volume: '10cm*10cm*10cm',
      quantity: 1,
      univalent: 100,
      salePrice: 100,
    },
  ],
  tabelHtml: '',
  columnsAttr: [
    {
      title: '',
      value: '{}',
      name: 'productName',
      style: {},
    },
    {
      title: '',
      value: '{}',
      name: 'brandName',
    },
    {
      title: '',
      value: '{}',
      name: 'specModel',
    },
    {
      title: '',
      value: '{}',
      name: 'productUnitName',
    },
    {
      title: '',
      value: '{}',
      name: 'weight',
    },
    {
      title: '',
      value: '{}',
      name: 'volume',
    },
    {
      title: '',
      value: '{}',
      name: 'quantity',
    },
    {
      title: '',
      value: '{}',
      name: 'univalent',
    },
    {
      title: '',
      value: '{}',
      name: 'salePrice',
    },
    {
      title: '',
      value: '{}',
      name: 'salePrice',
      type: 'row',
    },
  ],
  columns: [],
}

import vptd from '../../../mixins/vptd'

const WIDGET_NAME = 'braid-table'

export default {
  mixins: [vptd],
  name: WIDGET_NAME,
  setting: {
    type: WIDGET_NAME,
    isEdit: false, // 
    dragable: true, // 
    resizable: true, // 
    dynamic: false, // 
    width: 240,
    height: 60,
    left: 50,
    top: 10,
    title: '',
    value: [],
    defaultValue: [],
    tabelHtml: '',
    columnsAttr: [
      // {
      //   title: '',
      //   value: '{}',
      //   name: 'productName',
      // },
    ], // 
    columns: [], // 
    name: '',
    style: {
      zIndex: 0,
      Alignment: 1, //  1-- 2-- 3--
      FontSize: 9,
      FontColor: '#000000',
      BorderColor: '#000000',
      AutoHeight: 0, // 
      BottomMargin: 0, // 
    },
  },
  props: [
    'val', // 
  ],
  computed: {
    // type='row'
    columns() {
      let col = this.val.columns.filter((item) => item.type != 'row')
      return col
    },
    // type='row'
    rows() {
      let rows = this.val.columns.filter((item) => item.type == 'row')
      return rows
    },
    // 
    rowspan() {
      return 1 + this.rows.length
    },
    // 
    colspan() {
      let contentCol = this.columns.filter((item) => item.name !== '_seq')
      return contentCol.length
    },
  },
  methods: {},
}
</script>

